p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul {
  margin: 0;
  padding-left: 0;
}

img {
  width: 100%;
  max-width: 100%;
  height: 100%;
  display: block;
}

button {
  cursor: pointer;
}

.link {
  cursor: pointer;
  text-decoration: none;
}

.populars {
